<template>
	<div class="diy_edit page_appointment_record" id="appointment_record_edit">
		<div class='warp'>
			<div class='container'>
				<div class='row diy_edit_content_box'>
						<div v-if="$check_field('set','appointment_number') || $check_field('add','appointment_number') || $check_field('get','appointment_number')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								预约编号:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_appointment_number" v-model="form['appointment_number']" placeholder="请输入预约编号" v-if="(form['appointment_number'] && $check_field('set','appointment_number')) || (!form['appointment_number'] && $check_field('add','appointment_number'))" :disabled="true"/>
							<span v-else-if="$check_field('get','appointment_number')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','vaccine_no') || $check_field('add','vaccine_no') || $check_field('get','vaccine_no')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								疫苗编号:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_vaccine_no" v-model="form['vaccine_no']" placeholder="请输入疫苗编号" v-if="(form['vaccine_no'] && $check_field('set','vaccine_no')) || (!form['vaccine_no'] && $check_field('add','vaccine_no'))"  :disabled="disabledObj['vaccine_no_isDisabled']"/>
							<span v-else-if="$check_field('get','vaccine_no')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','vaccine_name') || $check_field('add','vaccine_name') || $check_field('get','vaccine_name')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								疫苗名称:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_vaccine_name" v-model="form['vaccine_name']" placeholder="请输入疫苗名称" v-if="(form['vaccine_name'] && $check_field('set','vaccine_name')) || (!form['vaccine_name'] && $check_field('add','vaccine_name'))"  :disabled="disabledObj['vaccine_name_isDisabled']"/>
							<span v-else-if="$check_field('get','vaccine_name')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','vaccine_classification') || $check_field('add','vaccine_classification') || $check_field('get','vaccine_classification')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								疫苗分类:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_vaccine_classification" v-model="form['vaccine_classification']" placeholder="请输入疫苗分类" v-if="(form['vaccine_classification'] && $check_field('set','vaccine_classification')) || (!form['vaccine_classification'] && $check_field('add','vaccine_classification'))"  :disabled="disabledObj['vaccine_classification_isDisabled']"/>
							<span v-else-if="$check_field('get','vaccine_classification')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','suitable_for_people') || $check_field('add','suitable_for_people') || $check_field('get','suitable_for_people')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								适合人群:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_suitable_for_people" v-model="form['suitable_for_people']" placeholder="请输入适合人群" v-if="(form['suitable_for_people'] && $check_field('set','suitable_for_people')) || (!form['suitable_for_people'] && $check_field('add','suitable_for_people'))"  :disabled="disabledObj['suitable_for_people_isDisabled']"/>
							<span v-else-if="$check_field('get','suitable_for_people')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','bookable_location') || $check_field('add','bookable_location') || $check_field('get','bookable_location')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								可预约地点:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_bookable_location" v-model="form['bookable_location']" placeholder="请输入可预约地点" v-if="(form['bookable_location'] && $check_field('set','bookable_location')) || (!form['bookable_location'] && $check_field('add','bookable_location'))"  :disabled="disabledObj['bookable_location_isDisabled']"/>
							<span v-else-if="$check_field('get','bookable_location')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','time_of_appointment') || $check_field('add','time_of_appointment') || $check_field('get','time_of_appointment')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								预约时间:
							</span>
						</div>
								<!-- 日长 -->
						<div class="diy_field diy_datetime">
							<input type="datetime-local" :disabled="disabledObj['time_of_appointment_isDisabled']" id="form_time_of_appointment" v-model="form['time_of_appointment']" placeholder="请输入预约时间" v-if="(form['time_of_appointment'] && $check_field('set','time_of_appointment')) || (!form['time_of_appointment'] && $check_field('add','time_of_appointment'))" />
							<span v-else-if="$check_field('get','time_of_appointment')">{{ form['${obj.name}'] }}</span>
						</div>
							</div>
							<div v-if="$check_field('set','number_of_appointments') || $check_field('add','number_of_appointments') || $check_field('get','number_of_appointments')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								预约数量:
							</span>
						</div>
								<!-- 选项 -->
						<div class="diy_field diy_down">
							<select id="form_number_of_appointments" v-model="form['number_of_appointments']" v-if="(form['number_of_appointments'] && $check_field('set','number_of_appointments')) || (!form['number_of_appointments'] && $check_field('add','number_of_appointments'))" >
								<option v-for="o in list_number_of_appointments" :value="o">
									{{o}}
								</option>
							</select>
							<span v-else-if="$check_field('get','number_of_appointments')">{{ form['number_of_appointments'] }}</span>
						</div>
							</div>
							<div v-if="$check_field('set','user_name') || $check_field('add','user_name') || $check_field('get','user_name')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								用户姓名:
							</span>
						</div>
						<div class="diy_field diy_down">
							<select id="form_user_name" :disabled="disabledObj['user_name_isDisabled']" v-model="form['user_name']" v-if="(form['user_name'] && $check_field('set','user_name')) || (!form['user_name'] && $check_field('add','user_name'))" >
								<option v-for="o in list_user_user_name" :value="o['user_id']">
									{{o['nickname'] + '-' + o['username']}}
								</option>
							</select>
							<span v-else-if="$check_field('get','user_name')">{{ form['user_name'] }}</span>
						</div>
					</div>
							<div v-if="$check_field('set','telephone') || $check_field('add','telephone') || $check_field('get','telephone')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								电话:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_telephone" v-model="form['telephone']" placeholder="请输入电话" v-if="(form['telephone'] && $check_field('set','telephone')) || (!form['telephone'] && $check_field('add','telephone'))"  :disabled="disabledObj['telephone_isDisabled']"/>
							<span v-else-if="$check_field('get','telephone')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
	




				</div>
				<div class="diy_edit_submit_box row">
					<div class="col-12">
						<div class="btn_box">
							<button class="btn_submit" @click="submit()">提交</button>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import mixin from "@/mixins/page.js";
	export default {
		mixins: [mixin],
		components: {},
		data() {
			return {
				url_get_obj: "~/api/appointment_record/get_obj?",
				url_add: "~/api/appointment_record/add?",
				url_set: "~/api/appointment_record/set?",

				// 登录权限
				oauth: {
					"signIn": true,
					"user_group": []
				},

				// 查询条件
				query: {
						"appointment_number": "",
							"vaccine_no": "",
							"vaccine_name": "",
							"vaccine_classification": "",
							"suitable_for_people": "",
							"bookable_location": "",
							"time_of_appointment": "",
							"number_of_appointments": "",
							"user_name": 0,
							"telephone": "",
						"appointment_record_id": 0,
				},

				obj: {
						"appointment_number": this.$get_stamp(), // 预约编号
							"vaccine_no":  '', // 疫苗编号
							"vaccine_name":  '', // 疫苗名称
							"vaccine_classification":  '', // 疫苗分类
							"suitable_for_people":  '', // 适合人群
							"bookable_location":  '', // 可预约地点
							"time_of_appointment": new Date().getTime(),
							"number_of_appointments":  '', // 预约数量
							"user_name": 0, // 用户姓名
							"telephone":  '', // 电话
						"appointment_record_id": 0,
				},

				// 表单字段
				form: {
						"appointment_number": this.$get_stamp(), // 预约编号
							"vaccine_no":  '', // 疫苗编号
							"vaccine_name":  '', // 疫苗名称
							"vaccine_classification":  '', // 疫苗分类
							"suitable_for_people":  '', // 适合人群
							"bookable_location":  '', // 可预约地点
							"time_of_appointment": new Date().getTime(),
							"number_of_appointments":  '', // 预约数量
							"user_name": 0, // 用户姓名
							"telephone":  '', // 电话
						"appointment_record_id": 0,
				},
				disabledObj:{
						"appointment_number_isDisabled": false,
							"vaccine_no_isDisabled": false,
							"vaccine_name_isDisabled": false,
							"vaccine_classification_isDisabled": false,
							"suitable_for_people_isDisabled": false,
							"bookable_location_isDisabled": false,
							"time_of_appointment_isDisabled": false,
							"number_of_appointments_isDisabled": false,
							"user_name_isDisabled": false,
							"telephone_isDisabled": false,
					},

																			// 预约数量选项列表
				list_number_of_appointments: ['1'],
								// 用户列表
				list_user_user_name: [],
			
				// ID字段
				field: "appointment_record_id",

			}
		},
		methods: {
			
				
				
				
				
				
				
				
						/**
			 * 获取普通用户用户列表
			 */
			async get_list_user_user_name() {
				// if(this.user_group !== "管理员" && this.form["user_name"] === 0) {
				//     this.form["user_name"] = this.user.user_id;
				// }
				var json = await this.$get("~/api/user/get_list?user_group=普通用户");
				if(json.result && json.result.list){
					this.list_user_user_name = json.result.list;
				}
				else if(json.error){
					console.error(json.error);
				}
			},
					async get_user_session_user_name(){
				var _this = this;
				var json = await this.$get("~/api/user_group/get_obj?name=普通用户");
				if(json.result && json.result.obj){
					var source_table = json.result.obj.source_table;
					var user_id = _this.$store.state.user.user_id;
					if (user_id){
						var url = "~/api/"+source_table+"/get_obj?"
						this.$get(url, {"user_id":_this.$store.state.user.user_id}, function(res) {
							if (res.result && res.result.obj) {
								var arr = []
								for (let key in res.result.obj) {
									arr.push(key)
								}
								var arrForm = []
								for (let key in _this.form) {
									arrForm.push(key)
								}
								_this.form["user_name"] = user_id
								_this.disabledObj['user_name' + '_isDisabled'] = true
								for (var i=0;i<arr.length;i++){
                  if (arr[i]!=='examine_state' && arr[i]!=='examine_reply') {
                    for (var j = 0; j < arrForm.length; j++) {
                      if (arr[i] === arrForm[j]) {
                        if (arr[i] !== "user_name") {
                          _this.form[arrForm[j]] = res.result.obj[arr[i]]
                          _this.disabledObj[arrForm[j] + '_isDisabled'] = true
                          break;
                        }
                      }
                    }
                  }
								}
							}
						});
					}
				}
				else if(json.error){
					console.error(json.error);
				}
			},
	
				
	
			/**
			 * 修改文件
			 * @param { Object } files 上传文件对象
			 * @param { String } str 表单的属性名
			 */
			change_file(files, str) {
				var form = new FormData();
				form.append("file", files[0]);
				this.$post("~/api/appointment_record/upload?", form, (res) => {
					if (res.result) {
						this.form[str] = res.result.url;
					} else if (res.error) {
						this.$toast(res.error.message);
					}
				});
			},

			/**
			 * 获取对象后获取缓存表单
			 * @param {Object} json
			 * @param {Object} func
			 */
			get_obj_before(param){
				var form = $.db.get("form");
				// if (form) {
        //   delete(form.examine_state)
        //   delete(form.examine_reply)
        //   this.obj = $.push(this.obj ,form);
				// 	this.form = $.push(this.form ,form);
				// }
				// var arr = []
				// for (let key in form) {
				// 	arr.push(key)
				// }
				// for (var i=0;i<arr.length;i++){
				// 	this.disabledObj[arr[i] + '_isDisabled'] = true
				// }
        if (form) {
          var arr = []
          for (let key in form) {
            arr.push(key)
          }
          var arrForm = []
          for (let key in this.form) {
            arrForm.push(key)
          }
          for (var i=0;i<arr.length;i++){
            if (arr[i]!=='examine_state' && arr[i]!=='examine_reply') {
              for (var j = 0; j < arrForm.length; j++) {
                if (arrForm[j] === arr[i]) {
                  this.form[arrForm[j]] = form[arr[i]]
                  this.obj[arrForm[j]] = form[arr[i]]
                  this.disabledObj[arrForm[j] + '_isDisabled'] = true
                  break;
                }
              }
            }
          }
        }
													        if (JSON.stringify(this.form["time_of_appointment"]).indexOf("-")===-1) {
          this.form["time_of_appointment"] = this.$toTime(parseInt(this.form["time_of_appointment"]), "yyyy-MM-ddThh:mm")
        }
								
        $.db.del("form");
				return param;
			},

			/**
			 * 获取对象后获取缓存表单
			 * @param {Object} json
			 * @param {Object} func
			 */
			get_obj_after(json ,func){
				// var form = $.db.get("form");
				// var obj = Object.assign({} ,form ,this.obj);
				// if (obj) {
        //   delete(obj.examine_state)
        //   delete(obj.examine_reply)
				// 	this.obj = $.push(this.obj ,obj);
				// }
				// if (form) {
        //   delete(form.examine_state)
        //   delete(form.examine_reply)
				// 	this.form = $.push(this.form ,form);
				// }

				if(func){
					func(json);
				}
			},

		},
		created() {
																													this.get_user_session_user_name();
					this.get_list_user_user_name();
						},
	}
</script>

<style>




</style>
